export function leadingThrottle(func, wait) {
    let previous = 0;
    const throttle = (...args) => {
        const now = Date.now();
        if (now - previous > wait) {
            func(args);
            previous = now;
        }
    };
    throttle.cancel = function () {
        previous = 0;
    };
    return throttle;
}
