export function trailingThrottle(func, wait) {
    let timeout = null;
    const throttle = (...args) => {
        if (!timeout) {
            timeout = setTimeout(() => {
                timeout = null;
                func(args);
            }, wait);
        }
    };
    throttle.cancel = function () {
        clearTimeout(timeout);
        timeout = null;
    };
    return throttle;
}
